











import Vue from 'vue';
export default Vue.extend({
	data() {
		return {
			file: '',
			title: '',
			playing: false
		};
	},
	props: {
		board: Number,
		holdMode: Boolean,
		index: Number
	},
	methods: {
		clear(e: MouseEvent) {
			e.preventDefault();
			this.stop();
			this.save(null);
			return false;
		},
		fileChange(fileChangeEvent: Event) {
			const fileInput = this.$refs.fileInput as HTMLInputElement;
			const files = fileInput.files as FileList;
			const file = files.item(0) as File;

			const reader = new FileReader();
			reader.addEventListener('load', readerLoadEvent => {
				const e = readerLoadEvent as ProgressEvent;
				const target = e.target as any;
				const buttonItem = {
					title: file.name.substring(0, file.name.lastIndexOf('.')),
					file: target.result
				};
				this.save(buttonItem);
			});

			if (!files.length) return;
			reader.readAsDataURL(file);
		},
		keyDown(key?: number) {
			if (key && key !== this.index) return;
			if (!this.holdMode && this.playing) return this.stop();
			this.play();
		},
		keyUp(key?: number) {
			if (key && key !== this.index) return;
			if (this.holdMode) this.stop();
		},
		load() {
			this.file = '';
			this.stop();
			const item = window.localStorage.getItem(this.storageIndex);
			if (item) {
				const buttonItem: ButtonItem = JSON.parse(item);
				this.title = buttonItem.title;
				this.file = buttonItem.file;
			} else {
				this.title = '';
				this.file = '';
			}
		},
		play() {
			if (!this.file.length) {
				const input = this.$refs.fileInput as HTMLInputElement;
				input.click();
			} else {
				this.playing = true;
				const audioElement = this.$refs.audio as HTMLAudioElement;
				audioElement.play();
			}
		},
		save(item: ButtonItem | null) {
			if (item === null) {
				window.localStorage.removeItem(this.storageIndex);
			} else {
				window.localStorage.setItem(
					this.storageIndex,
					JSON.stringify(item)
				);
			}
			this.load();
		},
		stop() {
			if (!this.playing) {
				return;
			}
			this.playing = false;
			const audioElement = this.$refs.audio as HTMLAudioElement;
			audioElement.pause();
			audioElement.currentTime = 0;
		}
	},
	computed: {
		storageIndex() {
			return `button-${this.board}-${this.index}`;
		}
	},
	watch: {
		// Reload when the board changes.
		board() {
			this.load();
		}
	},
	mounted() {
		this.load();
	}
});
